<template>
  <v-card>
    <v-card-text class="px-7">
      <showDataAccessHeader @add='add'
                            :showAdd='false'
                            :label="$t('查看下属人员数据')"
                            :readonly='readonly'
                            v-model="dataobj.mrbase_sys_data_strategy_subordinate_is_used" />
      <div v-for="(item,index) in list"
           :key='index'>
        <tree v-model="item.exclude_function_uuids"
              :readonly='readonly'
              maxHeight='800'></tree>
      </div>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       v-if="!readonly"
                       :cancelText="$store.state.uuid?$t('取消'):$t('label_The_previous_step')"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import {
  listData,
  getData,
  updateData,
  addData,
  delData,
  updatebasis
} from '@/service/api/data-access-strategy'
import showDataAccessHeader from '../showDataAccessHeader'
import dialogModule from './dialogModule'
import tree from '../tree'
export default {
  components: { showDataAccessHeader, dialogModule, tree },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      open: false,
      dataobj: {
        mrbase_sys_data_strategy_subordinate_is_used: '0'
      },
      list: [{ exclude_function_uuids: '' }],
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.uuid) {//修改
      await this.getList()
    }
    else {//新增
    }
  },

  methods: {
    async getList () {
      if (this.$store.state.uuid) {
        const res = await getData({ org_level_uuid: this.$store.state.uuid, date: 'mrbase_sys_data_strategy_subordinate' })
        if (res.code == 200) {
          this.list = res.mrbase_sys_data_strategy_subordinate ? res.mrbase_sys_data_strategy_subordinate : [{ exclude_function_uuids: '' }]
          this.dataobj = res.data ? res.data : {}
        }
      }
      else {

      }
    },
    add () {
      this.open = true
    },
    async success () {
      if (this.$store.state.uuid) {//修改
        let arr = this.list.map(item => {
          return { ...item, strategy_uuid: this.dataobj.uuid }
        })
        const res = await updateData({
          '~table~': 'mrbase_sys_data_strategy_subordinate',
          mrbase_sys_data_strategy_subordinate_is_used: this.dataobj.mrbase_sys_data_strategy_subordinate_is_used,
          mrbase_sys_data_strategy_subordinate: arr
        })
        if (res.code == 200) {
          this.$Message({ text: this.$t('修改成功') })
          await this.getList()
          this.$emit('updateSuccess')
        }
        else {
          this.$Message({ text: this.$t(res.msg), color: 'rgb(0, 0, 0, 0.38) ' })
        }
      }
      else {//新增
        this.$emit('success')
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  },
};
</script>

