<template>
  <div class="d-flex align-center justify-space-between checkBox">
    <template v-if="!$store.state.uuid">
      <mrc_ess_switch true-value="1"
                      false-value="0"
                      :readonly='readonly'
                      v-model="num"
                      :label='label'></mrc_ess_switch>
      <v-btn rounded
             v-if="showAdd"
             color="white"
             @click="addNewData"
             class='d-flex justify-center align-center px-5'>
        <span style="color:#2658c7;font-size: 25px;line-height:25px;height:28px"
              class="mr-1">+</span><span style="font-size: 14px;color: rgba(0, 0, 0, 0.78);">新数据</span>
      </v-btn>
      <span v-else></span>
    </template>
    <template v-else>
      <v-checkbox v-model="num"
                  :readonly='readonly'
                  true-value="1"
                  false-value="0"
                  style="font-size: 20px;color: rgba(0, 0, 0, 0.38);"
                  :label="label"></v-checkbox>
      <v-btn rounded
             v-if="showAdd"
             color="white"
             @click="addNewData"
             class='d-flex justify-center align-center px-5'>
        <span style="color:#2658c7;font-size: 25px;line-height:25px;height:28px"
              class="mr-1">+</span><span style="font-size: 14px;color: rgba(0, 0, 0, 0.78);">新数据</span>
      </v-btn>
      <span v-else></span>
    </template>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    label: {
      type: String,
      default: '查看自己录入数据'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    checkbox: {
      type: String,
      default: '0'
    },
    showAdd: {
      type: Boolean,
      default: true
    }
  },
  model: {
    prop: 'checkbox',
    event: 'change'
  },
  data () {
    return {
      num: null
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {
    checkbox (val) {
      this.num = val
    },
    num (val) {
      this.$emit('change', val)
    }
  },
  mounted () {
    this.num = this.checkbox
  },
  methods: {
    addNewData () {
      this.$emit('add')
    },
  },
};
</script>

<style scoped>
.checkBox /deep/ .v-label {
  font-size: 20px;
}
</style>
