<template>
  <mrc_ess_myDialog :isopen='isopen'
                    @cancel='cancel'
                    @success='success'
                    :width='550'
                    :title='title'
                    :disabled='true'>
    <mrc_ess_prompt :errMsg="$errors('params','name',requiredRule)"
                    :rules="false"
                    label="含功能名称"
                    class="mt-5"
                    placeholeder='功能名称/短编码'
                    obj='language_client'
                    v-model="params.name"></mrc_ess_prompt>
  </mrc_ess_myDialog>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    title: {
      type: String,
    },
    isopen: {
      type: Boolean,
      default: false
    },
    paramsData: {
      type: Object,
      default: () => { }
    }
  },
  model: {
    prop: 'isopen',
    event: 'flag'
  },
  data () {
    return {
      params: {
        name: ''
      }
    };
  },
  validations: {
    params: {
      name: {
        required
      }
    }
  },
  computed: {},
  created () { },
  watch: {},
  mounted () { },

  methods: {
    cancel () {
      this.$emit('flag', !this.isopen)
      this.$emit('cancel')
    },
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('getData', this.params)
      this.$emit('flag', !this.isopen)
    },
  },
};
</script>

